import React,{useState} from 'react';
import './App.css';
import './../../node_modules/bootstrap/dist/css/bootstrap.min.css';
import Parts from './Parts';

export default function App() {

    const [pcPart, setPcPart] = useState([
        { id: 1,name: 'screen', maxAmount: 5, option: {option1: '27"',option2: '52"',option3: '72"'}},
        { id: 2,name: 'Processor', maxAmount: 4, option: {option1: '4 core',option2: '8 core',option3: '16 core'}},
        { id: 3,name: 'Memory', maxAmount: 3, option: {option1: '256GB',option2: '512GB',option3: '1TB'}},
        { id: 4,name: 'OS', maxAmount: 2, option: {option1: 'Windows',option2: 'Linux',option3: 'Apple'}}
    ]);

const pcParts = pcPart;
    return (
        <>
        <div className="container-fluid bg-dark text-light pc-builder-container">
            <div className="text-center">
                <h1>Easy<span className="pc">PC</span></h1>
                <h4>Build Your Computers!</h4>
            </div>  
            <div className="row text-center">
                <div className="col-8">
                {pcParts.map(part => <Parts part={part}/>)} 
                </div>
                <div className="col-4">
                    <h1 className="bg-light text-dark py-1 card">your PC:</h1>
                </div>
            </div>
        </div>
        </>
    )
}
