import React from 'react'
import PcOptions from './PcOptions'
export default function Parts({ part }) {
    return (
    <>
    <div className="row text-center">
    <div className="col-12 d-flex justify-content-center">
        <h1>{part.name}</h1>
        <p className="ml-3 muted align-self-end">only {part.maxAmount} left</p>
    </div>
    <div className="row col-md-12">
        <div className="col-2">
            <button className="btn btn-light btn-block">-</button>
        </div>
            <PcOptions option={part.option}/>
        <div className="col-2">
            <button className="btn btn-light btn-block">+</button>
        </div>
    </div>
    </div>
    </>
    )
}
