import React from 'react';

export default function PcOption({option}){
    return (
    <div className="col-8">
        <button className="btn btn-outline-light p-1 m-1">{option.option1}</button>
        <button className="btn btn-outline-light p-1 m-1">{option.option2}</button>
        <button className="btn btn-outline-light p-1 m-1">{option.option3}</button>
    </div>
    )
}